.main-nav {
  position: relative;
}
@media (min-width: 768px) {
  .main-nav {
    width: 73%;
    margin-bottom: 28px;
  }
}
@media (min-width: 1400px) {
  .main-nav {
    width: 50%;
    margin-bottom: 60px;
  }
}

.main-nav__toggle {
  position: absolute;
  width: 48px;
  height: 48px;
  right: 20px;
  top: -65px;
  display: block;
  cursor: pointer;
  border: none;
  background-color: #ffffff;
}
@media (min-width: 1400px), (min-width: 768px) {
  .main-nav__toggle {
    display: none;
  }
}

@media (max-width: 767px) {
  .main-nav--open .main-nav__toggle::before, .main-nav--open .main-nav__toggle::after {
    content: "";
    position: absolute;
    right: -2px;
    width: 25px;
    height: 1.5px;
    background-color: #000000;
  }
  .main-nav--open .main-nav__toggle::before {
    transform: rotate(45deg);
  }
  .main-nav--open .main-nav__toggle::after {
    transform: rotate(-45deg);
  }
  .main-nav--open .main-nav__toggle:active::before, .main-nav--open .main-nav__toggle:active::after {
    background-color: rgba(0, 0, 0, 0.3);
  }
  .main-nav--closed .main-nav__toggle::before {
    content: "";
    position: absolute;
    right: 0;
    top: 16px;
    width: 24px;
    height: 1.5px;
    background-color: #000000;
    box-shadow: 0 8px 0 0 #000000, 0 16px 0 0 #000000;
  }
  .main-nav--closed .main-nav__toggle:active::before {
    background-color: rgba(0, 0, 0, 0.3);
    box-shadow: 0 8px 0 0 rgba(0, 0, 0, 0.3), 0 16px 0 0 rgba(0, 0, 0, 0.3);
  }
  .main-nav--nojs .main-nav__toggle {
    display: none;
  }
  .main-nav--closed .main-nav__wrapper {
    display: none;
  }
}
.main-nav__list {
  display: flex;
  flex-direction: column;
}
@media (min-width: 768px) {
  .main-nav__list {
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: flex-end;
  }
}

