.form__basic-info,
.form__purpose,
.form__contacts,
.form__comments,
.form__additionally {
  border: none;
  padding: 0;
  margin: 0;
}

.form__subtitle {
  font-size: 24px;
  line-height: 26px;
  padding: 0;

  @media (min-width: $tablet-width), (min-width: $desktop-width) {
    font-size: 36px;
    line-height: 36px;
  }
}

.form__input-wrap {
  display: flex;
  align-items: baseline;
  flex-basis: auto;
  flex-shrink: 0;

  margin-bottom: 14px;

  @media (min-width: $tablet-width) {
    margin-bottom: 18px;
  }

  @media (min-width: $desktop-width) {
    margin-bottom: 18px;
  }
}

.form__input {
  box-sizing: border-box;
  padding: 14px;
  border: 1px solid $grey-E7;
  margin-left: 14px;

  @media (min-width: $tablet-width) {
    padding: 13px 17px;
    padding-bottom: 10px;
    margin-left: 16px;
  }

  @media (min-width: $desktop-width) {
    margin-left: 20px;
    padding: 11px 20px;
  }
}

.form__input--textarea {
  padding-top: 18px;
  margin-left: 0;

  flex-grow: 1;

  @media (min-width: $tablet-width) {
    padding-top: 14px;

    height: 174px;
  }
}

.form__input--textarea::placeholder {
  font-size: 15px;
  line-height: 16px;

  @media (min-width: $tablet-width) {
    font-size: 20px;
    line-height: 30px;
  }
}

.form__input:hover {
  border: 1px solid $grey-CD;
}

.form__input:focus {
  border: 2px solid $grey-dark-special-444;
}

.form__input--error {
  border: 2px solid $red;
}

.form__input::placeholder {
  font-family: "Oswald", "Arial", sans-serif;
  color: $grey-dark-special-444;
}

.form__text {
  font-size: 16px;
  line-height: 18px;
  text-transform: uppercase;
  color: $grey-dark-special-444;

  @media (min-width: $tablet-width), (min-width: $desktop-width) {
    font-size: 20px;
    line-height: 30px;
  }
}


.form__input-wrap .form__text {
  flex-shrink: 0;
}

.form__input-wrap .form__input {
  flex: 1 1 auto;
  min-width: 172px;
}

.form__input-wrap:last-child {
  margin-bottom: 0;
}

.form__wrap-button {
  @media (min-width: $tablet-width) {
    padding-left: 70px;
    padding-right: 70px;
    padding-bottom: 72px;

    display: grid;
    grid-template-columns: 1fr 1fr;
    column-gap: 60px;
    align-items: center;
  }

  @media (min-width: $desktop-width) {
    padding-left: 110px;
    padding-right: 110px;
    padding-bottom: 82px;

    column-gap: 80px;
  }
}

.form__button {
  font-size: 16px;
  line-height: 20px;

  border: none;

  width: 280px;
  height: 40px;

  padding: 10px;
  margin: 0 auto;
  margin-bottom: 21px;

  @media (max-width: $mobile-max-width) {
    grid-column: 1/-1;
  }

  @media (min-width: $tablet-width) {
    font-size: 20px;
    line-height: 0px;

    width: 100%;
    height: 52px;

    margin: 0;
  }
}

.form__notice {
  font-family: "Arial", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 16px;

  display: block;
  width: 158px;
  margin: 0 auto;
  margin-bottom: 32px;


  @media (max-width: $mobile-max-width) {
    grid-column: 1/-1;
  }

  @media (min-width: $tablet-width) {
    font-size: 16px;
    line-height: 16px;
    white-space: nowrap;

    margin: 0;
  }
}

.form__wrap-info-and-purpose {
  @media (min-width: $desktop-width) {
    padding: 0 110px;
    padding-bottom: 84px;

    display: grid;
    grid-template-columns: repeat(2, 1fr);
    column-gap: 80px;
  }
}
